#intro {
    height: calc(100vh - 4rem);
    width: 100wv;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.bg {
    position: absolute;
    top: -3rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}

.introcontent {
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.Hello {
    font-size: 1.75rem;
    font-weight: 100;
}

.introname {
    color: yellow;
}

.introPara {
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}

.btn {
    background-color: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}

.btnimg {
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

@media  screen and (max-width:840px) {
        .bg {
            right: 10vw 
        }
        .introcontent {
                font-size: 10vw; 
        }
        .Hello {
            font-size: 4.5vw;
        }
}

@media  screen and (max-width:400px) {
    .bg {
        right: -20vw
    }
}
